import React, {FC, useState} from "react";
import {Box, Paper, Typography} from "@mui/material";
import {DataGrid, GridColumns, GridRowModel} from "@mui/x-data-grid";
import GenerateId from "../../../../global-component/GenerateId";

interface iTableField {
    id: string,
    field1: string,
    field2: string,
    field3: string
}


const VchnPreparatoryData: FC = () => {

    //       
    const columns: GridColumns = [
        {
            field: 'field1',
            headerName: '',
            width: 600,
            editable: false,
            type: "number",
            headerAlign: 'center',
            align: 'left'
        },
        {
            field: 'field2',
            headerName: '',
            width: 150,
            editable: true,
            type: "number",
            headerAlign: 'center',
            align: 'center'
        },
        {
            field: 'field3',
            headerName: '',
            width: 300,
            editable: false,
            type: "number",
            headerAlign: 'center',
            align: 'center'
        },
    ]

    const [rowsTbPo, setRowsTbPo] = useState<iTableField[]>([
        {
            id: GenerateId(),
            field1: '   U , ',
            field2: '',
            field3: ' '
        },
        {
            id: GenerateId(),
            field1: '       D, ',
            field2: '',
            field3: ' '
        },
        {
            id: GenerateId(),
            field1: '  1   ; 2   ; 3  . ;',
            field2: '',
            field3: ' '
        },
        {
            id: GenerateId(),
            field1: '  / ',
            field2: '',
            field3: '   .  '
        },
        {
            id: GenerateId(),
            field1: '     W',
            field2: '',
            field3: '   .   '
        },
        {
            id: GenerateId(),
            field1: '    hP , ',
            field2: '',
            field3: ' '
        },
        {
            id: GenerateId(),
            field1: '    E.. , /(0,5)',
            field2: '',
            field3: ' '
        },
        {
            id: GenerateId(),
            field1: '        , /',
            field2: '',
            field3: ' '
        },
    ]);

    //     
    const processRowUpdate = React.useCallback(
        async (newRow: GridRowModel) => {
            setRowsTbPo(rowsTbPo.map(n => n.id === newRow.id ? newRow : n))
            return newRow;
        },
        [rowsTbPo],
    );

    return (
        <React.Fragment>
            <Paper elevation={4}>
                <Box p={1}>
                    <Typography variant={'h6'} pb={2}> </Typography>
                    <div style={{height: 400, width: '100%'}}>
                        <DataGrid
                            rows={rowsTbPo}
                            columns={columns}
                            showCellRightBorder={true}
                            showColumnRightBorder={true}
                            processRowUpdate={processRowUpdate}
                            experimentalFeatures={{newEditingApi: true}}
                        />
                    </div>
                </Box>
            </Paper>
        </React.Fragment>
    );
};

export default VchnPreparatoryData;